import { configureStore } from '@reduxjs/toolkit'
import authSlice from './slices/authSlice'
import pondsSlice from './slices/pondsSlice'
import userSlice from './slices/userSlice'

export default configureStore({
  reducer: {
    auth:authSlice,
    ponds: pondsSlice,
    users: userSlice
  },
})
