import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import { JsonClient } from '../../config'
const initialState = {
  data: [],
  status: 'idle',
  error: ''
}

export const fetchPondsData = createAsyncThunk('wbs/fetchPondsData', async (data) => {
  const response = await JsonClient.post('devices/data/with/assets/',data)
  console.log("all ponds data", response)
  return response.data
})

export const pondsSlice = createSlice({
  name: 'ponds',
  initialState,
  reducers: {

  },
  extraReducers: {
    [fetchPondsData.pending]: (state, action) => {
      state.status = 'loading'
    },
    [fetchPondsData.fulfilled]: (state, action) => {
      state.status = 'succeeded'
      // Add any fetched posts to the array
      state.data = action.payload
    },
    [fetchPondsData.rejected]: (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    },
  }
})

// Action creators are generated for each case reducer function

export default pondsSlice.reducer
